<template>
	<div>


			<div class="left">
				<h1>我的文章</h1>
				<div class="box" v-for='j in article'>
					<h2>
						<a href="">文章:{{j.article_name}}</a>
						<router-link :to='{path:"/main/every",query:{id:j.id}}'>详情》》</router-link>
					</h2>
					<div class="msg">
						<span>发表时间:{{j.time}}</span>
						<span>作者:{{j.editer}}</span>
					</div>
					<div class="bot">
						<img src="../../static/img/9.jpg" alt="">
						<p>{{j.content}}</p>
					</div>

				</div>

			</div>





	</div>
</template>
<script>
import {
	mapActions,
	mapMatations,
	mapState
} from 'vuex'

	export default{
		name:"index",
		data:function(){
			return {
				currentPage1: 5,
		        currentPage2: 3,
		        currentPage3: 5,
		        currentPage4: 4
			}
		},
		created(){

			this.getTwo(this);


		},
		computed:{
			...mapState({

				article:state=>state.a.article
			})
		},
		methods:{
			...mapActions(['getTwo']),

		},
		components:{

		}
	}
</script>


