<template>
	<div class="container">
		<div class="wrapper">
			<h2>{{data.article_name}}</h2>
			<div class="bottom">
				<h3>
					<span>上传时间:{{data.time}}</span>
					<span>作者:{{data.editer}}</span>
				</h3>
				<p>{{data.content}}</p>
			</div>
		</div>

	</div>
</template>
<style>
*{
	box-sizing:border-box;
}

	.container .wrapper{
		width: 800px;
		float: left;

	}

	h2{
		font-size: 14px;
		border-bottom: 1px solid #96CEB4;
		height: 30px;
		line-height: 30px;
	}
	h3{
		height: 30px;
		line-height: 30px;
		display: flex;
		justify-content:space-between;
		align-items:center;
	}
	h3 span{
		font-weight: 500;
	}
	p{
		font-size: 15px;
		letter-spacing:2px;
	}
</style>
<script>
import {
	mapState,
	mapActions
} from 'vuex'

	export default{
		data:function(){
			return {

				data:null
			}
		},
		created(){
			var that=this;
			// console.log(this.article)
			var itemId=this.$route.query.id;

			this.axios.get('api/front_article/getArticle?id='+itemId).then(function(data){
				// console.log(data.data)
				if(data.data.code=='6030'){
					that.data=data.data.data;
					console.log(data.data.data)
				}
			})


		},
		computed:{
			...mapState({
				article:state=>state.a.article
			})
		},
		components:{

		}
	}
</script>


