import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import main from '@/components/main'
import every from '@/components/every'
import toggle from '@/components/toggle'
import index from '@/components/index'
import other from '@/components/other'
import loading from '@/components/loading'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/main',
      name: 'main',
      component: main,
      redirect:"/main/index",
      children:[
      	{
      		path:'index',
      		name:'index',
      		component:index
      	},
      	{
      		path:'every',
      		name:'every',
      		component:every
      	}
      ]
    },
    {
      path: '/other',
      name: 'other',
      component: other
    },
    {
      path: '/toggle',
      name: 'toggle',
      component: toggle
    },
    {
      path: '/loading',
      name: 'loading',
      component: loading
    }
  ]
})



