<template>
	<div>
		1111
	</div>
</template>
<script>
import {
	mapActions,
	mapState
} from 'vuex'
	export default{

		created(){
			this.getTwo(this);
			this.getData(this);
			this.$router.push({name:'main'})
		},
		methods:{
			...mapActions(['getTwo','getData'])
		},
		computed:{
			...mapState({
				article:state=>state.a.article,
				navData:state=>state.a.navData
			})
		}
	}
</script>


