<template>
	<div>
		<div class="top">
			<input type="text" v-model='val'><button @click='searchData(val)'>搜索</button>
		</div>
		<ul class="sum">
					<li v-for='i in article'><a href=""><router-link :to='{path:"/other",query:{id:i.id}}'>{{i.article_name}}</router-link></a></li>
		</ul>
	</div>
</template>
<script>
import {
	mapActions,
	mapMatations
} from 'vuex'
	export default{
		props:['article'],
		data:function(){
			return {
				val:''
			}
		},
		methods:{
			...mapActions(['searchData'])
		}
	}
</script>
<style>
	.top{
		width: 100%;
		display: flex;
		margin-top: 10px;
	}
	.top input{
		width: 75%;
		height: 25px;
		outline: none;
	}
	.top button{
		width: 25%;
		height: 25px;
		background: lightseagreen;
		color: #fff;
		border:0;
		outline: none;
	}
</style>


