<template>
	<div>
		<header>
		   <nav id="nav">
		    <ul>

				<li v-for='item in navData'><router-link :to='{name:"toggle",query:{id:item.id}}'>{{item.cnname}}</router-link></li>
		    </ul>
		  </nav>
		</header>
	<div class="mainContent">
		  <div class="ulist">
		    <section> <a href="#"> <img src="../../static/img/8.jpg"></a>
		      <ul>
		        <h2><a href="#">李钟情</a></h2>
		        <p>生活明朗,万物可爱</p>
		      </ul>
		    </section>

		  </div>
		</div>
		<div class="mainContent2">
			<router-view></router-view>

			<div class="right">
				<list :article='article'></list>

			</div>

		 </div>

	</div>
</template>
<style>
*{
	box-sizing:border-box;
}
	h1{
		font-size: 16px;

	}
	.left{
		width:800px;
		padding:0 10px;
		float: left;
		border-right: 2px dotted #999;
	}
	.right{
		width: 198px;
		height: 600px;
		background: #96CEB4;
		float: left;
		padding:0 10px;

	}
	.left h2{
		display: flex;
		height: 35px;
		line-height: 35px;
		justify-content:space-between;
		align-items:center;
		width:100%;
		border-bottom: 1px solid orange;
	}
	.left h2 a{
		font-size: 14px;
		color: orange;
	}
	.left .box .msg span{
		font-size: 14px;
		font-weight: 400;

	}
	.left .box .bot p{
		flex:1;

		overflow : hidden;
	    text-overflow: ellipsis;
	    display: -webkit-box;
	    -webkit-line-clamp:1;
	    -webkit-box-orient: vertical;
		color: #000;
	}
	.left .box{
		height: 130px;
		margin-top: 10px;
	}
	.left .box .bot{
		display: flex;
	}
	.left .box .bot img{
		width: 80px;
		height: 80px;
	}
	.box .msg{
		width: 100%;
		height: 30px;
		display: flex;
		justify-content:space-between;
		align-items:center;
	}
	.sum{
		width: 100%;
		padding:0 10px;
	}
	.sum li{
		height: 30px;
		line-height: 30px;
		text-align: center;
		border-bottom: 1px dotted #7CB0A0;
	}
	.sum li a{
		color: #fff;
	}
</style>
<script>
import {
	mapActions,
	mapMatations,
	mapState
} from 'vuex'
import list from './list'
	export default{
		name:"main",
		data:function(){
			return {
				currentPage1: 5,
		        currentPage2: 3,
		        currentPage3: 5,
		        currentPage4: 4
			}
		},
		created(){
			this.getData(this);
			console.log(this.article)

		},
		beforeRouteEnter(to,from,next){
			// console.log(to.params)
			next(vm=>{
				console.log(vm.$store.state.a)
				if(!vm.$store.state.a){
					vm.$router.push({path:'/loading'})
				}else{
					vm.article=vm.$store.state.a.article;
					vm.navData=vm.$store.state.a.navData;
				}
			})
		},
		computed:{
			...mapState({
				navData:state=>state.a.navData,
				article:state=>state.a.article
			})
		},
		methods:{
			...mapActions(['getData','getTwo']),

		},
		components:{
			list
		}
	}
</script>


