<template>
	<div>
		<ul class="two">
			<li v-for='i in two'>{{i.cnname}}</li>
		</ul>
	</div>
</template>
<style>
	*{
		padding:0 10px;
		box-sizing:border-box;
	}
	.two{
		width: 600px;
		background: #fff;
	}
	.two li{
		height: 30px;
		line-height: 30px;
		border-bottom: 1px solid #ccc;
	}
</style>
<script>
	export default{
		data:function(){
			return {
				two:null
			}
		},
		created(){
			var that=this;
			console.log(this.$route.query)
			var oneid=this.$route.query.id;
			this.axios.post('api/front_article/getClassTwo',{
				oneId:oneid
			}).then(function(data){
				console.log(data.data)
				if(data.data.code=='6020'){
					that.two=data.data.data;
				}
			})
		}
	}
</script>


