<template>
  <div class="hello">
      <div class="self">
        <dl>
          <dt><img src="../../static/img/8.jpg" alt=""></dt>
          <dd>
              <h2>李阳个人blog</h2>
              <p>我会长成一棵大树等你赞一声良木</p>
          </dd>
        </dl>
        <ul class="list">
          <li>
            <router-link to='/loading'>blog</router-link>

          </li>
          <li>
              <router-link to='/person'>个人简介</router-link>

          </li>
        </ul>
      </div>
      <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
  box-sizing:border-box;
}
.hello{
  widows: 1200px;
  height:600px;
  background: url(../../static/img/6.jpg) no-repeat;
  background-size: 100% 100%;
  position: relative;
}
.self{
  width:500px;
  height: 300px;
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom: 0;
  margin:auto;
  border:2px solid orange;
  padding:0 10px;
  border-radius: 10px;
}
.self dl{
  width:100%;
  display: flex;
}
.self dl dt{
  width:120px;
  height: 120px;
  overflow: hidden;
  background: red;
  border-radius: 50%;
}
.self dl dt img{
  width:100%;
  height: 100%;
}
.self dl dd{
  padding-left: 20px;
}
.self dl dd h2{
  height: 35px;
  line-height: 35px;
  margin-top: 20px;
  color: orange;
}
.self dl dd p{
  color:limegreen;
  margin-top: 10px;
  font-size: 14px;
}
.list{
  width: 90%;
  height: 50px;

  margin: 20px auto;
  display: flex;
}
.list li{
 width:45%;
background:rgba(200,100,200,0.5);
  text-align: center;
  line-height: 50px;
  margin-left:2.5%;

  border-radius: 10px;
}
.list li a{
  font-size: 17px;
  color: #fff;
}
</style>



